	<view class="container">
		<!-- È°∂ÈÉ®Âá∏ÂºßËÉåÊôØ -->
		<view class="header-container">
			<svg class="bottom-arch" viewBox="0 0 750 120" preserveAspectRatio="none">
				<path d="M0,0 C300,60 450,60 750,0 L750,120 L0,120 Z" fill="#b5d6f2" />
				<text x="375" y="50" text-anchor="middle" dominant-baseline="middle" class="app-title">ÊàêÂ∞±</text>
			</svg>
		</view>

		<!-- ÂÜÖÂÆπÂå∫Âüü -->
		<scroll-view class="content-area" scroll-y>
			<view class="content-wrapper">
				<!-- Èí±ÂåÖËøûÊé•Âå∫Âüü -->
				<view class="wallet-section">
					<view class="wallet-card" v-if="!walletConnected">
						<view class="wallet-info">
							<text class="wallet-title">üîó Âå∫ÂùóÈìæÊàêÂ∞±</text>
							<text class="wallet-description">ËøûÊé•Èí±ÂåÖÊü•ÁúãÊÇ®ÁöÑNFTÊàêÂ∞±</text>
						</view>
						<button 
							class="connect-btn" 
							@click="connectWallet"
							:loading="isConnectingWallet"
						>
							{{ isConnectingWallet ? 'ËøûÊé•‰∏≠...' : 'ËøûÊé•Èí±ÂåÖ' }}
						</button>
					</view>
					
					<view class="wallet-card connected" v-else>
						<view class="wallet-info">
							<text class="wallet-title">‚úÖ Èí±ÂåÖÂ∑≤ËøûÊé•</text>
							<text class="wallet-address">{{ formatAddress(walletAddress) }}</text>
							<text class="wallet-network">{{ networkName || 'Injective Testnet' }}</text>
						</view>
						<button class="disconnect-btn" @click="disconnectWallet">Êñ≠ÂºÄ</button>
					</view>
				</view>

				<view class="achievement-stats">
					<view class="stat-card" id="total-questions">
						<text class="stat-number">{{ userStats.totalQuestions }}</text>
						<text class="stat-label">ÂÆåÊàêÈ¢òÁõÆ</text>
					</view>
					<view class="stat-card" id="streak-days">
						<text class="stat-number">{{ userStats.streakDays }}</text>
						<text class="stat-label">ËøûÁª≠Â§©Êï∞</text>
					</view>
					<view class="stat-card" id="total-points">
						<text class="stat-number">{{ userStats.totalPoints }}</text>
						<text class="stat-label">ÊÄªÁßØÂàÜ</text>
					</view>
					<view class="stat-card" id="total-nfts" v-if="walletConnected">
						<text class="stat-number">{{ userStats.totalNFTs }}</text>
						<text class="stat-label">NFTÊàêÂ∞±</text>
					</view>
				</view>

				<view v-if="loading" class="loading">
					<text>Âä†ËΩΩ‰∏≠...</text>
				</view>

				<view v-else>
					<!-- Âå∫ÂùóÈìæÊàêÂ∞±Âå∫Âüü -->
					<view v-if="walletConnected && blockchainAchievements.length > 0" class="section-title">
						<text>üèÜ Èìæ‰∏äNFTÊàêÂ∞±</text>
					</view>
					
					<view v-if="walletConnected && blockchainAchievements.length > 0" class="blockchain-achievements">
						<view 
							v-for="nft in blockchainAchievements" 
							:key="nft.tokenId"
							class="achievement-card blockchain-achievement"
						>
							<view class="achievement-icon">
								<view class="nft-badge">
									<text class="nft-icon">{{ getNFTIcon(nft.achievementType) }}</text>
									<view class="nft-verified">
										<text class="verified-text">‚úì</text>
									</view>
								</view>
							</view>
							<view class="achievement-info">
								<view class="achievement-header">
									<text class="achievement-title">{{ nft.name }}</text>
									<view class="nft-tag">
										<text>NFT #{{ nft.tokenId }}</text>
									</view>
								</view>
								<text class="achievement-description">{{ nft.description }}</text>
								<view class="achievement-details">
									<text class="achievement-type">Á±ªÂûã: {{ getAchievementTypeName(nft.achievementType) }}</text>
									<text class="achievement-time">Èì∏ÈÄ†: {{ formatDate(nft.timestamp) }}</text>
								</view>
							</view>
						</view>
					</view>

					<!-- ‰º†ÁªüÊàêÂ∞±Âå∫Âüü -->
					<view class="section-title">
						<text>üìã Á≥ªÁªüÊàêÂ∞±</text>
					</view>
					<view class="achievement-grid">
						<view 
							v-for="(achievement, index) in achievements" 
							:key="achievement.id"
							class="achievement-card"
							:class="{ 'unlocked': achievement.unlocked, 'blockchain-linked': achievement.nftMinted }"
						>
							<view class="achievement-icon">
								<svg :id="`achievement-${index}`" width="80" height="80"></svg>
								<view v-if="achievement.nftMinted" class="blockchain-indicator">
									<text>üîó</text>
								</view>
							</view>
							<view class="achievement-info">
								<view class="achievement-header">
									<text class="achievement-title">{{ achievement.title }}</text>
									<view v-if="achievement.nftMinted" class="nft-linked-tag">
										<text>Â∑≤‰∏äÈìæ</text>
									</view>
								</view>
								<text class="achievement-description">{{ achievement.description }}</text>
								<view class="achievement-progress">
									<text>{{ achievement.progress }}/{{ achievement.total }}</text>
									<text v-if="achievement.earnedAt" class="achievement-date">{{ formatDate(achievement.earnedAt) }}</text>
								</view>
							</view>
						</view>
					</view>
				</view>
			</view>
		</scroll-view>

		<!-- Â∫ïÈÉ®dockerÊ†è -->
		<view class="bottom-bg">
			<image src="/static/docker/Ê¢ØÂΩ¢.svg" mode="aspectFill"></image>
		</view>

		<view class="bottom-docker">
			<view class="trapezoid t1" @click="navigateTo('map')">
				<image style="z-index: 20;margin-left: 40rpx;" src="/static/docker/Âú∞Âõæ.svg" class="docker-icon" mode="aspectFit"></image>
			</view>
			<view class="trapezoid t2" @click="navigateTo('daily')">
				<image style="z-index: 20;margin-left: 40rpx;" src="/static/docker/Êó•Êé®.svg" class="docker-icon" mode="aspectFit"></image>
			</view>
			<view class="trapezoid t3 active" @click="navigateTo('achievement')">
				<image style="z-index: 20;margin-left: 40rpx;" src="/static/docker/ÊàêÂ∞±.svg" class="docker-icon" mode="aspectFit"></image>
			</view>
			<view class="trapezoid t4" @click="navigateTo('profile')">
				<image style="z-index: 20;margin-left: 40rpx;" src="/static/docker/Êàë.svg" class="docker-icon" mode="aspectFit"></image>
			</view>
		</view>
	</view>
</template>

<script>
import rough from 'roughjs'
import { getAllAchievements, getUserAchievements } from '@/api/achievements'
import { 
    initWeb3, 
    getCurrentAddress, 
    getUserAllAchievements, 
    subscribeToWalletEvents, 
    subscribeToAchievementEvents,
    unsubscribeFromWalletEvents,
    unsubscribeFromAchievementEvents
} from '@/utils/web3.js'
import { ethers } from 'ethers';
export default {
	data() {
		return {
			achievements: [],
			userStats: {
				totalQuestions: 0,
				streakDays: 0,
				totalPoints: 0,
				totalAchievements: 0,
				totalNFTs: 0
			},
			loading: false,
			// Âå∫ÂùóÈìæÁõ∏ÂÖ≥Áä∂ÊÄÅ
			web3Initialized: false,
			walletConnected: false,
			walletAddress: null,
			blockchainAchievements: [],
			isConnectingWallet: false,
			chainId: null,
			networkName: null
		}
	},
	mounted() {
		this.$nextTick(() => {
			this.loadAchievements()
			
			// Ê∑ªÂä†Âå∫ÂùóÈìæ‰∫ã‰ª∂ÁõëÂê¨
			this.setupBlockchainEvents()
		})
	},
	beforeDestroy() {
		// Ê∏ÖÁêÜ‰∫ã‰ª∂ÁõëÂê¨
		unsubscribeFromWalletEvents()
		unsubscribeFromAchievementEvents()
	},
	methods: {
		async loadAchievements() {
			try {
				this.loading = true
				
				// Ëé∑ÂèñÊâÄÊúâÊàêÂ∞±ÂíåÁî®Êà∑ÊàêÂ∞±
				const [allAchievementsRes, userAchievementsRes] = await Promise.all([
					getAllAchievements(),
					getUserAchievements()
				])
				
				// Ëé∑ÂèñÁî®Êà∑Â∑≤Ëé∑ÂæóÁöÑÊàêÂ∞±IDÂàóË°®
				const userAchievementIds = (userAchievementsRes.data || []).map(ua => ua.achievement_id)
				
				// Â§ÑÁêÜÊàêÂ∞±Êï∞ÊçÆ
				this.achievements = allAchievementsRes.data.map(achievement => {
					// Ê£ÄÊü•ÊòØÂê¶ÊòØ"È¶ñÊàòÂëäÊç∑"ÊàêÂ∞±ÔºåÂ¶ÇÊûúÊòØÂàôÂº∫Âà∂ËÆæÁΩÆ‰∏∫Â∑≤Ëß£ÈîÅ
					const isFirstVictory = achievement.id === 'f1ee1a1f-77ea-4df8-b8e4-d214512140bf'
					const isUnlocked = isFirstVictory ? true : userAchievementIds.includes(achievement.id)
					
					const userAchievement = userAchievementsRes.data?.find(ua => ua.achievement_id === achievement.id)
					
					// Ê†πÊçÆÊàêÂ∞±Á≠âÁ∫ßËÆæÁΩÆ‰∏çÂêåÁöÑÊ†∑Âºè
					const levelStyles = {
						1: { color: '#CD7F32', name: 'Bronze' }, // ÈìúÁâå
						2: { color: '#C0C0C0', name: 'Silver' }, // Èì∂Áâå
						3: { color: '#FFD700', name: 'Gold' }    // ÈáëÁâå
					}
					
					// Ëé∑ÂèñÊàêÂ∞±Ëé∑ÂæóÊó∂Èó¥
					const earnedAt = isFirstVictory ? new Date() : (userAchievement ? new Date(userAchievement.earned_at) : null)
					
					return {
						id: achievement.id,
						title: achievement.name,
						description: achievement.description,
						iconUrl: achievement.icon_url,
						level: achievement.level,
						unlocked: isUnlocked,
						earnedAt: earnedAt,
						levelStyle: levelStyles[achievement.level] || { color: '#ff7f50', name: 'Default' },
						nftMetadata: achievement.nft_metadata,
						progress: isUnlocked ? 1 : 0,
						total: 1
					}
				})
				
				// Êõ¥Êñ∞Áî®Êà∑ÁªüËÆ°Êï∞ÊçÆÔºåÁ°Æ‰øùÂåÖÂê´"È¶ñÊàòÂëäÊç∑"
				this.userStats = {
					...this.userStats,
					totalAchievements: Math.max(1, userAchievementIds.length) // Ëá≥Â∞ëÊúâ‰∏Ä‰∏™ÊàêÂ∞±
				}
				
				// Â¶ÇÊûúÈí±ÂåÖÂ∑≤ËøûÊé•ÔºåÂä†ËΩΩÂå∫ÂùóÈìæÊàêÂ∞±
				if (this.walletConnected) {
					await this.loadBlockchainAchievements()
				}
				
				// ÁªòÂà∂ÊàêÂ∞±ÂõæÊ†á
				this.$nextTick(() => {
					this.drawAchievementIcons()
				})
			} catch (error) {
				console.error('Âä†ËΩΩÊàêÂ∞±Â§±Ë¥•:', error)
				uni.showToast({
					title: error.message || 'Âä†ËΩΩÊàêÂ∞±Â§±Ë¥•',
					icon: 'none'
				})
			} finally {
				this.loading = false
			}
		},
		
		// Èí±ÂåÖËøûÊé•ÊñπÊ≥ï
		async connectWallet() {
			if (this.isConnectingWallet) return;
			
			this.isConnectingWallet = true;
			try {
				// Initialize Web3
				const { provider, signer } = await initWeb3();
				this.web3Initialized = true;
				
				// Get address
				const address = await signer.getAddress();
				this.walletAddress = address;
				this.walletConnected = true;
				
				// Get network info
				const network = await provider.getNetwork();
				this.chainId = network.chainId.toString();
				this.networkName = 'Injective Testnet';
				
				// Load achievements
				await this.loadBlockchainAchievements();
				
				uni.showToast({
					title: 'Èí±ÂåÖËøûÊé•ÊàêÂäü',
					icon: 'success'
				});
			} catch (error) {
				console.error('ËøûÊé•Èí±ÂåÖÂ§±Ë¥•:', error);
				uni.showToast({
					title: error.message || 'ËøûÊé•Èí±ÂåÖÂ§±Ë¥•',
					icon: 'none'
				});
				this.web3Initialized = false;
				this.walletConnected = false;
			} finally {
				this.isConnectingWallet = false;
			}
		},
		
		// Êñ≠ÂºÄÈí±ÂåÖËøûÊé•
		disconnectWallet() {
			this.walletConnected = false
			this.walletAddress = null
			this.blockchainAchievements = []
			this.userStats.totalNFTs = 0
			this.web3Initialized = false
			
			uni.showToast({
				title: 'Èí±ÂåÖÂ∑≤Êñ≠ÂºÄ',
				icon: 'success'
			})
		},
		
		// Âä†ËΩΩÂå∫ÂùóÈìæÊàêÂ∞±
		async loadBlockchainAchievements() {
			if (!this.walletAddress) return
			
			try {
				const blockchainAchievements = await getUserAllAchievements(this.walletAddress)
				this.blockchainAchievements = blockchainAchievements
				this.userStats.totalNFTs = blockchainAchievements.length
				
				// Ê†áËÆ∞Â∑≤‰∏äÈìæÁöÑÊàêÂ∞±
				this.achievements = this.achievements.map(achievement => {
					const isMinted = blockchainAchievements.some(nft => 
						nft.name === achievement.title || 
						nft.description === achievement.description
					)
					return {
						...achievement,
						nftMinted: isMinted
					}
				})
				
			} catch (error) {
				console.error('Âä†ËΩΩÂå∫ÂùóÈìæÊàêÂ∞±Â§±Ë¥•:', error)
				// ÈùôÈªòÂ§ÑÁêÜÈîôËØØÔºå‰∏çÂΩ±Âìç‰º†ÁªüÊàêÂ∞±ÊòæÁ§∫
			}
		},
		
		// ËÆæÁΩÆÂå∫ÂùóÈìæ‰∫ã‰ª∂ÁõëÂê¨
		setupBlockchainEvents() {
			subscribeToWalletEvents({
				onAccountsChanged: (accounts) => {
					if (accounts.length === 0) {
						this.disconnectWallet()
					} else {
						this.walletAddress = accounts[0]
						this.loadBlockchainAchievements()
					}
				},
				onChainChanged: (chainId) => {
					this.chainId = chainId
					if (this.walletConnected) {
						this.loadBlockchainAchievements()
					}
				},
				onDisconnect: () => {
					this.disconnectWallet()
				}
			})
			
			subscribeToAchievementEvents({
				onAchievementMinted: () => {
					// ÈáçÊñ∞Âä†ËΩΩÂå∫ÂùóÈìæÊàêÂ∞±
					this.loadBlockchainAchievements()
					uni.showToast({
						title: 'Êñ∞NFTÊàêÂ∞±Â∑≤Èì∏ÈÄ†',
						icon: 'success'
					})
				}
			})
		},
		
		// Ê†ºÂºèÂåñÂú∞ÂùÄ
		formatAddress(address) {
			if (!address) return ''
			return address.substring(0, 6) + '...' + address.substring(address.length - 4)
		},
		
		// Ê†ºÂºèÂåñÊó•Êúü
		formatDate(date) {
			if (!date) return ''
			const d = new Date(date)
			return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')}`
		},
		
		// Ëé∑ÂèñÊàêÂ∞±Á±ªÂûãÂêçÁß∞
		getAchievementTypeName(type) {
			const types = {
				0: 'ÈùíÈìú',
				1: 'ÁôΩÈì∂', 
				2: 'ÈªÑÈáë',
				3: 'ÈíªÁü≥'
			}
			return types[type] || 'Êú™Áü•'
		},
		
		// ÂéüÊúâÁöÑÁªòÂà∂ÊàêÂ∞±ÂõæÊ†áÊñπÊ≥ï‰øùÊåÅ‰∏çÂèò
		drawAchievementIcons() {
			this.achievements.forEach((achievement, index) => {
				const svg = document.getElementById(`achievement-${index}`)
				if (svg) {
					// Ê∏ÖÈô§Áé∞ÊúâÂÜÖÂÆπ
					while (svg.firstChild) {
						svg.removeChild(svg.firstChild)
					}
					
					const rc = rough.svg(svg)
					
					// ÁªòÂà∂ÊàêÂ∞±ÂæΩÁ´†Â§ñÊ°Ü
					const badge = rc.circle(40, 40, 70, {
						stroke: achievement.unlocked ? achievement.levelStyle.color : '#ccc',
						strokeWidth: 2,
						fill: achievement.unlocked ? '#fff1e6' : '#f5f5f5',
						roughness: 1.5,
						bowing: 1
					})
					svg.appendChild(badge)
					
					// ‰∏∫Â∑≤Ëß£ÈîÅÁöÑÊàêÂ∞±ÁªòÂà∂ÊòüÊòü
					if (achievement.unlocked) {
						// ÁªòÂà∂ÂèëÂÖâÊïàÊûú
						const glow = rc.circle(40, 40, 75, {
							stroke: achievement.levelStyle.color,
							strokeWidth: 1,
							fill: 'none',
							roughness: 0.5,
							bowing: 1
						})
						svg.appendChild(glow)
						
						// ÁªòÂà∂ÊòüÊòü
						const star = rc.path('M40,15 L45,30 L60,30 L48,40 L52,55 L40,47 L28,55 L32,40 L20,30 L35,30 Z', {
							fill: achievement.levelStyle.color,
							stroke: achievement.levelStyle.color,
							strokeWidth: 1,
							roughness: 1.2,
							fillStyle: 'solid'
						})
						svg.appendChild(star)
						
						// Ê∑ªÂä†Â∞èË£ÖÈ•∞ÁÇπ
						const dots = [
							[60, 20], [20, 20], [40, 10],
							[65, 40], [15, 40], [40, 70]
						]
						dots.forEach(([x, y]) => {
							const dot = rc.circle(x, y, 3, {
								fill: achievement.levelStyle.color,
								stroke: 'none',
								roughness: 0.8,
								fillStyle: 'solid'
							})
							svg.appendChild(dot)
						})
					}
				}
			})
		},
		navigateTo(page) {
			const routes = {
				map: '/pages/map/map',
				daily: '/pages/daily/daily',
				achievement: '/pages/goal/goal',
				profile: '/pages/profile/profile'
			}
			
			if (page !== 'achievement') {
				uni.navigateTo({
					url: routes[page]
				})
			}
		}
	}
}
</script>

<style>
